.pip {
    background: var(--colorDisabled);
    border-radius: 100rem;
    display: inline-block;
    height: 1rem;
    width: 1rem;

    &.success {
        background: var(--colorSuccess50);
    }

    &.primary {
        background: var(--colorPrimary50);
    }

    &.warning {
        background: var(--colorInfo50);
    }

    &.danger {
        background: var(--colorError50);
    }
}
