.panel,
.errorPanel {
    border: solid 1px var(--colorScaffoldingHighlight);
    border-radius: 6px;
    height: auto;
    max-width: 960px;
    padding: 2.4rem;
}

.errorPanel {
    border-color: var(--colorError50);
    overflow: auto;
}

.container {
    display: grid;
    grid-gap: 16px;
    max-width: 720px;
}

.form {
    display: grid;
    grid-gap: 8px;
}

.sqlInput {
    border: solid 1px var(--colorScaffoldingHighlight);
    display: block;
    font-family: var(--fontFamilyMonospace);
    line-height: var(--lineHeightBody);
    padding: 0.8rem;
    resize: vertical;
    width: 100%;
}

.codeContainer {
    max-width: 100%;
    overflow: auto;
}

.buttons {
    margin: 1.6rem 0 0.8rem 0;
}
