.spinner {
    border: 0.25em solid;
    border-color: theme('colors.vtblue.50');
    border-right-color: transparent;
    vertical-align: -0.125em;

    @apply animate-spin;
    @apply border-2;
    @apply h-8;
    @apply inline-block;
    @apply rounded-full;
    @apply w-8;
}
