.headingMeta {
    display: flex;
}

.headingMeta span {
    display: inline-block;
    line-height: 2;

    &::after {
        color: var(--colorScaffoldingHighlight);
        content: '/';
        display: inline-block;
        margin: 0 1.2rem;
    }

    &:last-child::after {
        content: none;
    }
}

.placeholder {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-size: theme('fontSize.lg');
    justify-content: center;
    margin: 25vh auto;
    max-width: 720px;
    text-align: center;

    h1 {
        margin: 1.6rem 0;
    }
}

.errorEmoji {
    display: block;
    font-size: 5.6rem;
}
